@keyframes textDecoration {
  0% {
    text-decoration: underline solid transparent; /* transparent is what makes the transition smoother */
  }
  33% {
    text-decoration: underline solid rgb(170, 0, 153);
  }
  100% {
    text-decoration: underline solid transparent;
  }
}

.login {
  margin: 18px;
  font-size: 32px;
  color: rgb(0, 198, 172);
  animation: 2.6s infinite forwards textDecoration;
}

.underline {
  text-decoration: underline solid rgb(89, 148, 0);
}
.underline:hover {
  text-decoration: underline solid rgb(138, 231, 0);
}
