body {
  background: linear-gradient(
      rgb(0, 40, 36) 0%,
      rgb(28, 0, 23) 20%,
      rgb(29, 0, 7) 66%,
      rgb(21, 0, 0) 90%,
      rgb(0, 0, 0) 100%
    )
    fixed;
  text-align: center;
  color: rgb(44, 108, 255);
  font-size: 26px;
}

h1 {
  margin: 0;
}

input,
button,
option,
select {
  background-color: transparent;
  color: rgb(0, 198, 86);
  border: 2px solid rgb(65, 0, 60);
  border-radius: 4px;
  padding: 4px;
  margin: 6px;
  font-size: 20px;
}

input[type="checkbox"] {
  margin: 10px;
  width: 17px;
  height: 17px;
  accent-color: rgb(0, 159, 212); /*the color inside the checkbox when checked*/
}
input[type="checkbox"]:hover {
  outline: 4px groove rgb(169, 202, 109);
  cursor: pointer;
}
input[type="checkbox"]:checked {
  outline: 6px solid rgb(185, 0, 86);
  cursor: pointer;
}

.listUl {
  display: block;
  list-style-type: decimal;
  padding: 4px;
  margin: 0;
  width: fit-content;
  align-items: center;
}

.listDiv button {
  width: 100px;
  margin: 0px 10px 0px 0px;
}

.listItems {
  display: block;
  font-size: 26px;
  font-weight: 500;
  text-align: left;
  color: rgb(169, 202, 109);
}
