.siteTitle {
  position: relative;
  float: left;
  font-size: 3rem;
  font-weight: 500;
  background: linear-gradient(
    90deg,
    rgb(128, 0, 255) 2%,
    rgb(181, 0, 151) 22%,
    rgb(0, 188, 169) 60%,
    rgb(189, 148, 0) 79%,
    rgb(183, 43, 0) 98%
  ); /* Define your gradient here */
  -webkit-background-clip: text; /* Clip the background to the text */
  -webkit-text-fill-color: transparent; /* Make the text color transparent */
  text-decoration: none;
  padding: 0px 0px 0px 56px;
}

.nav {
  border-bottom: 7px solid;
  border-image: linear-gradient(to right, rgb(0, 56, 85) 50%, rgb(75, 0, 115) 80%) 100% 1;
}

.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.nav ul li {
  float: right;
  font-size: 24px;
  font-weight: 500;
}

.nav ul li a:not(.site-title),
.dropbtn {
  padding: 16px 16px;
  display: inline-block;
  text-decoration: none;
}

.dropA {
  width: fit-content;
}

.dropdownContent {
  position: absolute;
  display: none;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.nav ul li a:hover {
  color: rgb(155, 0, 142);
}

.nav ul li:hover :not(.site-title, .drop-a, .dropdown-content) {
  background: linear-gradient(to top, rgb(0, 24, 30) 0%, rgb(48, 0, 44) 100%) local;
}

.activeLink {
  background: linear-gradient(to top, rgb(0, 29, 37) 20%, rgb(70, 0, 128) 50%, rgb(0, 35, 47) 80%) local;
  color: inherit;
}
